import React from "react";
import { ImSad } from "react-icons/im";
import { MdAccountCircle, MdOutlineArrowForward } from "react-icons/md";
import { connect } from "react-redux";
// import { Link } from "react-router-dom";
import {
  Auth,
  DistrictList,
  DocumentCandidate,
  DocumentsValidationType,
  FC_ApproveDocument,
  FC_GetDistrictList,
  FC_GetDocuments,
  FC_TransferDocumentByDisability,
  TransferDisabilityList,
} from "../../actions";
import Alert from "../../components/Alert/Alert";
import Button from "../../components/Buttons/Button";
import DashboardContainer from "../../components/DashboardContainer/DashboardContainer";
import Heading from "../../components/Heading/Heading";
import TextInput from "../../components/Inputs/TextInput";
import Loading from "../../components/Loading/Loading";
import Modal, { ModalSize, Themes } from "../../components/Modal/Modal";
import PdfViewer from "../../components/PdfViewer/PdfViewer";
import { APP_URL_FILE } from "../../config/app.config";
import { StoreState } from "../../reducers";
import { search } from "../../utils/functions";

// props for the component
interface AppProps {
  auth: Auth;
}

interface AppState {
  loading: boolean;
  error: {
    target: "main-error" | "main-success" | null;
    msg: string;
  };
  limit: number;
  page: number;
  documents: DocumentCandidate[];
  districts: DistrictList[];
  total: number;
  selected_user: string;
  decision_residence: boolean | null;
  decision_disability: boolean | null;
  search_district: string;
  loading_districts: boolean;
  selected_district: DistrictList | null;
  status: DocumentsValidationType;
  selected_document_id: string | null;
  next: number | null;
  previous: number | null;
  loading_document_file: boolean;
  loading_document_decision: boolean;
  loading_disabled_by_district: boolean;
  total_disabled_by_district: string;
  documents_by_districts: TransferDisabilityList[];
  total_validated: string;
}

const defaultError = {
  target: null,
  msg: "",
};

class _ApproveDocuments extends React.Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      loading: false,
      loading_districts: false,
      districts: [],
      search_district: "",
      error: defaultError,
      documents: [],
      limit: 10,
      page: 1,
      total: 0,
      selected_user: "",
      decision_disability: null,
      decision_residence: null,
      selected_district: null,
      status: "remain",
      selected_document_id: "",
      next: null,
      previous: null,
      loading_document_file: true,
      loading_document_decision: false,
      loading_disabled_by_district: true,
      total_disabled_by_district: "0",
      documents_by_districts: [],
      total_validated: "0",
    };
  }

  componentDidMount() {
    FC_GetDistrictList((status, data, error) => {
      this.setState({
        districts: data,
        loading_districts: false,
        error:
          status === true
            ? defaultError
            : {
                target: "main-error",
                msg: `Districts: ${error}`,
              },
        documents: [],
      });
    });

    this.getTotalDistrictsNumber();
  }

  selectDistrict(district: DistrictList | null) {
    this.setState({
      selected_district: district,
      page: 1,
      total: 0,

      documents: [],
    });

    if (district !== null) {
      this.loadingData(
        district.district_code,
        this.state.page,
        this.state.status
      );
    }

    if (district === null) {
      this.getTotalDistrictsNumber();
    }
  }

  setDocumentDecision = (document_id: string | null, decision: boolean) => {
    if (
      !window.confirm(
        `Are you sure you want to ${
          decision === true ? "Approve" : "Reject"
        } this document?`
      )
    )
      return;

    if (!this.props.auth.user || !this.props.auth.user.user_id)
      return this.setState({
        error: {
          target: "main-error",
          msg: "Your id is not valid, please refresh your browser",
        },
      });

    if (!document_id)
      return this.setState({
        error: {
          msg: "No selected document",
          target: "main-error",
        },
      });

    this.setState({
      loading_document_decision: true,
    });
    FC_ApproveDocument(
      {
        disability: decision,
        residence: false,
        teacher_transfer_id: document_id,
        user_id: this.props.auth.user?.user_id,
      },
      (status, data, msg) => {
        // console.log({ status, data, msg });
        this.setState({
          loading_document_decision: false,
          documents:
            status === true
              ? this.state.documents.filter(
                  (el) => el.teacher_transfer_id !== document_id
                )
              : this.state.documents,
          error:
            status === true
              ? {
                  msg: `The document have been ${
                    decision === true ? "Approved" : "Rejected"
                  }`,
                  target: "main-success",
                }
              : {
                  msg: `Error: ${msg}`,
                  target: "main-error",
                },
        });

        this.clearAlert();
      }
    );
  };

  changeStatus = (value: DocumentsValidationType) => {
    if (this.state.status === value || !this.state.selected_district) return;
    this.setState({
      status: value,
    });
    this.loadingData(this.state.selected_district.district_code, 1, value);
  };

  clearAlert = () =>
    setTimeout(() => {
      this.setState({
        error: defaultError,
      });
    }, 3000);

  paginateData = (page: number) => {
    if (this.state.selected_district)
      this.loadingData(this.state.selected_district.district_code, page);
  };

  loadingData = (
    district: string,
    page: number,
    status?: DocumentsValidationType
  ) => {
    this.setState({
      loading: true,
      selected_document_id: null,
      documents: [],
    });
    FC_GetDocuments(
      {
        district_code: district,
        limit: this.state.limit,
        page: page,
        status: status ? status : this.state.status,
      },
      (status_req, data, msg) => {
        this.setState({
          loading: false,
          documents: data?.teacher_docs ? data?.teacher_docs : [],
          total: data?.total_transfer ? data.total_transfer : 0,
          next: data?.next ? +data?.next : 0,
          previous: data?.previous ? +data?.previous : 0,
          error: !status_req
            ? {
                target: "main-error",
                msg: `Error: ${msg}`,
              }
            : defaultError,
          page: page,
        });
      }
    );
  };

  getTotalDistrictsNumber = () => {
    this.setState({
      loading_disabled_by_district: true,
      documents_by_districts: [],
      total_disabled_by_district: "0",
      total_validated: "0",
    });
    FC_TransferDocumentByDisability((status, data, msg) => {
      this.setState({
        loading_disabled_by_district: false,
        documents_by_districts: data ? data.districts : [],
        total_disabled_by_district: data ? data.total : "0",
        total_validated: data ? data.total_validated : "0",
        error: status
          ? {
              msg: "",
              target: null,
            }
          : {
              msg: msg,
              target: "main-error",
            },
      });
    });
  };

  openLink = (link: string | null) => {
    if (link) window.open(`${APP_URL_FILE}/${link}`);
  };

  render() {
    let total_pages = (this.state.total / this.state.limit).toFixed(0);
    let foundDocument = this.state.documents.find(
      (transf) => transf.teacher_transfer_id === this.state.selected_document_id
    );
    let DistrictResults: DistrictList[] = search(
      this.state.districts,
      this.state.search_district,
      {
        district_code: true,
        district_name: true,
      }
    );

    // * List of districts
    if (this.state.selected_district === null) {
      return (
        <DashboardContainer className="animate__animated animate__zoomIn animate__faster">
          <div>
            <div className="flex items-center">
              <h1 className="flex-1 text-2xl font-bold">
                Documents validation
              </h1>
              <section className="p-1 bg-green-100 rounded mx-2">
                <span className="text-sm text-green-800">
                  Total:{" "}
                  <b className="text-black">
                    {this.state.total_disabled_by_district}
                  </b>
                </span>
              </section>
              <section className="p-1 bg-green-100 rounded">
                <span className="text-sm text-green-800">
                  Total validated:{" "}
                  <b className="text-black">{this.state.total_validated}</b>
                </span>
              </section>
            </div>
            <div className="p-2">
              <TextInput
                error=""
                label=""
                name=""
                onChange={(e) =>
                  this.setState({ search_district: e.target.value })
                }
                placeholder="Search district by name"
                value={this.state.search_district}
              />
            </div>

            {this.state.loading_districts === true ? (
              <div className="py-8 text-center text-8xl text-accent-900">
                <Loading className="mx-auto" />
              </div>
            ) : this.state.districts.length >= 1 ? (
              <>
                {DistrictResults.length <= 0 ? (
                  <div className="py-4 text-center">
                    <h2 className="text-lg">No results found!</h2>
                    <p className="text-gray-500">
                      Please use a correct district name
                    </p>
                  </div>
                ) : (
                  <div className="grid lg:grid-cols-2 gap-2 ">
                    {DistrictResults.map((dist) => {
                      let FoundDocuments =
                        this.state.documents_by_districts.find(
                          (el) => el.district_code === dist.district_code
                        );
                      return (
                        <div
                          onClick={this.selectDistrict.bind(this, dist)}
                          key={dist.district_code}
                          className="p-2 flex items-center cursor-pointer rounded border  px-3 hover:text-yellow-600 hover:border-yellow-600 hover:bg-yellow-50 bg-white text-lg"
                        >
                          <h4 className="flex-1 text-lg">
                            {" "}
                            <b>{dist.district_name}</b> district
                          </h4>

                          {FoundDocuments && FoundDocuments.total !== "0" && (
                            <span className="bg-yellow-100 text-yellow-700 font-bold py-0.5 inline-block text-sm px-1.5 rounded-full">
                              {FoundDocuments?.total}
                            </span>
                          )}

                          {FoundDocuments && FoundDocuments.total !== "0" && (
                            <button className="">
                              <MdOutlineArrowForward />
                            </button>
                          )}
                        </div>
                      );
                    })}
                  </div>
                )}
              </>
            ) : (
              <div className="py-4 text-center">
                <h2 className="text-8xl text-gray-400 text-center">
                  <ImSad className="inline" />
                </h2>
                <h2 className="text-center p-4 text-gray-400 font-bold text-2xl">
                  <p>No results found on</p>
                </h2>{" "}
              </div>
            )}
          </div>
        </DashboardContainer>
      );
    }

    // DOcuments list
    return (
      <>
        {this.state.error.target === "main-success" && (
          <Alert
            theme="success"
            fixed
            onClick={() => this.setState({ error: defaultError })}
          >
            {this.state.error.msg}
          </Alert>
        )}

        {this.state.error.target === "main-error" && (
          <Alert
            theme="danger"
            fixed
            onClick={() => this.setState({ error: defaultError })}
          >
            {this.state.error.msg}
          </Alert>
        )}

        {foundDocument !== undefined && this.state.selected_document_id && (
          <Modal
            backDrop={true}
            theme={Themes.default}
            close={() => this.setState({ selected_document_id: null })}
            backDropClose={false}
            widthSizeClass={ModalSize.maxWidth}
            displayClose={true}
            title={`${foundDocument.full_name}'s document`}
            padding={{
              title: true,
              body: undefined,
              footer: undefined,
            }}
          >
            <div className=" border-t h-full flex">
              <div className="flex-1 bg-gray-600">
                <PdfViewer
                  file_url={`${APP_URL_FILE}/${foundDocument.disability_surpoting_document}`}
                  class_name={"w-full h-100vh"}
                  frame_title={"User document"}
                  setLoadingFile={() =>
                    this.setState({ loading_document_file: false })
                  }
                  full_height={true}
                />
              </div>
              <div className="w-96">
                <h2 className="text-gray-500 p-1 px-2 bg-gray-100 text-2xl text-center">
                  Teacher's information
                </h2>

                {this.state.error.target === "main-success" && (
                  <Alert
                    theme="success"
                    onClick={() => this.setState({ error: defaultError })}
                  >
                    {this.state.error.msg}
                  </Alert>
                )}

                {this.state.error.target === "main-error" && (
                  <Alert
                    theme="danger"
                    onClick={() => this.setState({ error: defaultError })}
                  >
                    {this.state.error.msg}
                  </Alert>
                )}

                <section className="border-b py-4">
                  <h2 className="text-8xl text-gray-500">
                    <MdAccountCircle className="mx-auto" />
                  </h2>
                  <h3 className="text-center font-bold text-xl">
                    {foundDocument.full_name}
                  </h3>
                </section>

                <div className="p-2">
                  {[
                    { title: "DOB: ", value: foundDocument.dob },
                    { title: "Hire date: ", value: foundDocument.hire_date },
                    {
                      title: "Martial Status: ",
                      value: foundDocument.martial_status,
                    },
                    {
                      title: "Position name: ",
                      value: foundDocument.position_name,
                    },
                    {
                      title: "Qualification: ",
                      value: foundDocument.qualification_name,
                    },
                    {
                      title: "Residence: ",
                      value: foundDocument.resident_district_name,
                    },
                    { title: "Role: ", value: foundDocument.role },
                    {
                      title: "School name: ",
                      value: foundDocument.school_name,
                    },
                  ].map(({ title, value }, i) => (
                    <div
                      key={i}
                      className="flex items-center px-1 py-1.5 border-b"
                    >
                      <section className="text-gray-600 pr-2">{title}</section>
                      <section className="font-bold">{value}</section>
                    </div>
                  ))}
                </div>

                <div className="py-2 text-center flex gap-2 px-3">
                  <Button
                    size="sm"
                    theme="default"
                    onClick={this.openLink.bind(
                      this,
                      foundDocument.disability_surpoting_document
                    )}
                  >
                    Open document in a new tab
                  </Button>

                  <a
                    href={`${APP_URL_FILE}/${foundDocument.disability_surpoting_document}`}
                    rel="noreferrer"
                    target="_blank"
                    download
                  >
                    <Button size="sm" theme="primary-light">
                      Download
                    </Button>
                  </a>
                </div>

                <div className="p-2">
                  <Button
                    className="w-full"
                    theme="success-light"
                    size="lg"
                    onClick={this.setDocumentDecision.bind(
                      this,
                      foundDocument.teacher_transfer_id,
                      true
                    )}
                    loading={this.state.loading_document_decision}
                  >
                    Yes, the document is valid
                  </Button>
                </div>
                <div className="p-2">
                  <Button
                    className="w-full"
                    theme="danger-light"
                    size="lg"
                    onClick={this.setDocumentDecision.bind(
                      this,
                      foundDocument.teacher_transfer_id,
                      false
                    )}
                    loading={this.state.loading_document_decision}
                  >
                    No, the document is not valid
                  </Button>
                </div>
              </div>
            </div>
          </Modal>
        )}

        <div className="animate__animated animate__zoomIn animate__faster pt-2">
          <div className="container mx-auto px-2 flex items-center">
            <Button
              onClick={this.selectDistrict.bind(this, null)}
              theme="default"
            >
              Back
            </Button>
            <Heading className="flex-1 m-0">Document validation</Heading>
            <section>
              <div className="flex flex-1 gap-1 mr-2 bg-yellow-100 p-1 rounded border border-yellow-400">
                <Button
                  size="sm"
                  theme={
                    this.state.status === "remain" ? "accent" : "accent-light"
                  }
                  onClick={this.changeStatus.bind(this, "remain")}
                >
                  Remain
                </Button>
                <Button
                  size="sm"
                  theme={
                    this.state.status === "complete" ? "accent" : "accent-light"
                  }
                  onClick={this.changeStatus.bind(this, "complete")}
                >
                  Completed
                </Button>
              </div>
            </section>
            <section className="text-center bg-accent-800 bg-opacity-20 text-accent-900 px-4 rounded">
              <span className="text-sm block text-gray-500 -mb-1">Total:</span>
              <h3 className="font-bold text-xl">{this.state.total}</h3>
            </section>
          </div>

          <DashboardContainer>
            <table className="w-full">
              <thead className="text-left bg-white">
                <tr className="bg-accent-900 bg-opacity-10">
                  <th className="p-2 border-gray-300 border">#</th>
                  <th className="p-2 border-gray-300 border">Names</th>
                  <th className="p-2 border-gray-300 border">School Code</th>
                  <th className="p-2 border-gray-300 border">School name</th>
                  <th className="p-2 border-gray-300 border">Position code</th>
                  <th className="p-2 border-gray-300 border">Position name</th>
                  <th className="p-2 border-gray-300 border">Qualification</th>
                </tr>
              </thead>
              <tbody>
                {this.state.loading === true ? (
                  <tr className=" ">
                    <td colSpan={8} className="py-4 text-center border">
                      <Loading />
                    </td>
                  </tr>
                ) : this.state.documents.length <= 0 ? (
                  <tr>
                    <td colSpan={8} className="py-4 text-center border">
                      <h1 className="text-2xl text-gray-400">No data found</h1>
                      {/* <p className="text-gray"></p> */}
                    </td>
                  </tr>
                ) : (
                  this.state.documents.map((itm, i) => (
                    <tr
                      key={i}
                      className="bg-white cursor-pointer hover:bg-yellow-400 hover:bg-opacity-30"
                      onClick={() =>
                        this.setState({
                          selected_document_id: itm.teacher_transfer_id,
                          // transfer_issue: itm.transfer_issue_comment
                          //   ? itm.transfer_issue_comment
                          //   : "",
                        })
                      }
                    >
                      <td className="border p-2">{i + 1}</td>
                      <td className="p-2 border">{itm.full_name}</td>
                      <td className="p-2 border">{itm.school_from_id}</td>
                      <td className="p-2 border">{itm.school_name}</td>
                      <td className="p-2 border">{itm.position_id}</td>
                      <td className="p-2 border text-sm">
                        {itm.position_name}
                      </td>
                      <td className="p-2 border">{itm.qualification_name}</td>
                    </tr>
                  ))
                )}
              </tbody>
            </table>

            <div className="flex gap-3 mx-auto pt-3">
              {Array(+total_pages)
                .fill("0")
                .map((e, i) => (
                  <button
                    className={`p-2 py-1 ${
                      +this.state.page === i + 1
                        ? "bg-accent-800 text-black font-bold"
                        : "bg-opacity-25"
                    } rounded-full bg-accent-800 `}
                    onClick={this.paginateData.bind(this, i + 1)}
                  >
                    {i + 1}
                  </button>
                ))}
            </div>
          </DashboardContainer>
        </div>
      </>
    );
  }
}

const mapStateToProps = ({ auth }: StoreState): { auth: Auth } => {
  return { auth: auth };
};

export const ApproveDocuments = connect(mapStateToProps, {})(_ApproveDocuments);
